
import { fetchEndpoint } from '../server/network'  
 
// 新消息&新意见反馈回复状态 
export const getNew = () => fetchEndpoint('/user/XFUser/NewMessageFeedback', 'get')

// 消息列表
export const getMessageList = params => fetchEndpoint('/user/XFUser/MessageList', 'POST', false, params)

// 消息详情
export const getMessageInfo = params => fetchEndpoint('/user/XFUser/MessageInfo', 'POST', false, params)
 
// 意见反馈列表
export const getFeedback = params => fetchEndpoint('/user/XFUser/FeedbackList', 'POST', false, params) 

// 意见反馈详情
export const getFeedbackInfo = params => fetchEndpoint('/user/XFUser/FeedbackInfo', 'POST', false, params) 
 
// 公告列表
export const getNoticeList = params => fetchEndpoint('/user/XFUser/NoticeList ', 'POST', false, params) 

// 公告列表
export const getNoticeInfo = params => fetchEndpoint('/user/XFUser/NoticeInfo ', 'POST', false, params) 



