import {
  Toast
} from 'vant'
import axios from 'axios'
import utils from '../util/util'
import qs from 'qs'
import store from '../store/index'
import router from "../router";

// const root_limit = process.env.VUE_APP_APPSERVER_HOST
// const root_limit = 'http://test.zichanhuayuan.com'
// const root_limit = 'http://qa1.zichanhuayuan.com'
const root_limit = 'https://service.zichanhuayuan.com'

// 新建一个axios实例，不受全局的其他axios影响
const instance = axios.create({
  baseURL: root_limit,
  timeout: 10000
})

export function fetchEndpoint1(reqUrl, type = 'POST', isSplicing = false, data = {}) {
  if (!reqUrl) {
    return
  }

  // isSplicing 是否把参数拼接在url后
  if (isSplicing && data && typeof data !== 'object') reqUrl = reqUrl + data

  type = type.toUpperCase()

  if (type == 'GET' || type === 'PUT') {
    let dataStr = '' //数据拼接字符串
    Object.keys(data).forEach(key => {
      if (data[key] || data[key] == 0) {
        dataStr += key + '=' + data[key] + '&'
      }
    })

    if (dataStr !== '') {
      dataStr = dataStr.substr(0, dataStr.lastIndexOf('&'))
      reqUrl = reqUrl + '?' + dataStr
    }
  }

  // 给Get url添加随机串防止缓存
  if (type === 'GET') {
    const randomStr = new Date().getTime() + Math.random()
    reqUrl += (reqUrl.indexOf('?') > -1 ? '&' : '?') + `randomGetKey=${randomStr}`
  }

  // token
  let token = null
  if (store.getters.token) {
    token = store.getters.token
  }
  let post_body = qs.stringify(data)

  return new Promise((resolve, reject) => {
    instance
      .request({
        method: type,
        url: reqUrl,
        responseType: "blob",
        data: post_body,
        headers: {
          'X-WJ-AUTHORIZATION': token,
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      })
      .then(
        res => {
          console.log(res);
          resolve(res.data)
          if (res.data.code == 1016) {
            Toast(res.data.info)
            store.commit('clearToken', '')
            router.push({
              name: 'login'
            })
            return
          }
          if (res.data.code && res.data.code != 0) {
            Toast(res.data.info)
          }
        },
        error => {
          console.log('error', error)
          if (error.errorMsg) {
            Toast(error.errorMsg)
          } else if (/^\<\!DOCTYPE/.test(error)) {
            // 接口代码报错
            // Toast('网络繁忙，连接失败')
            router.push({
              path: '/noNetWork'
            })
          } else {
            Toast('网络繁忙，请稍后重试')
          }
          // 不把服务端的错误reject给业务层
          resolve({
            code: -1,
            data: {}
          })
        }
      )
  })
}

function logResponseParams(response, url) {
  if (process.env.NODE_ENV === 'development') {
    let str = JSON.stringify(response.data || response)
    console.groupCollapsed(
      '%c' + utils.formatDate('HH:mm:ss.SSS') + '%c请求' + url + '\n',
      'font-weight:normal;color:#fff;background:#35495e;padding:2px 7px;border-radius:3px 0 0 3px;',
      'font-weight:normal;color:#fff;background:#0066cc;padding:2px 7px;border-radius:0 3px 3px 0;',
      str.length < 80 ? str : str.substring(0, 80) + '...'
    )
    console.count('SN')
    // console.info(response);
    console.log('%c响应\n', 'color:#aaa', response.data)
    console.groupEnd()
  }
}

// 自定义判断元素类型JS
function toType(obj) {
  return {}.toString
    .call(obj)
    .match(/\s([a-zA-Z]+)/)[1]
    .toLowerCase()
}
// 参数过滤函数
function filterNull(o) {
  for (var key in o) {
    if (o[key] === null) {
      delete o[key]
    }
    if (toType(o[key]) === 'string') {
      o[key] = o[key].trim()
    } else if (toType(o[key]) === 'object') {
      o[key] = filterNull(o[key])
    } else if (toType(o[key]) === 'array') {
      o[key] = filterNull(o[key])
    }
  }
  return o
}



