import {
  fetchEndpoint
} from '../server/network'

// 设置交易密码
export const setPassWordRequest = params =>
  fetchEndpoint('/user/XFUser/SetPassWord', 'POST', false, {
    new_password: params.password1,
    token: params.token,
  })

// 修改交易密码
export const changePWRequest = params =>
  fetchEndpoint('/user/XFUser/EditPassWord', 'POST', false, {
      old_password : params.old_password,
      new_password : params.new_password,
  })

//找回/重置交易密码
export const findPWRequest = params =>
  fetchEndpoint('/user/XFUser/ResetPassword', 'POST', false, {
      new_password : params.new_password,
      code : Number(params.code)
  })


//获取短信验证码
export const getMsgRequest = params =>
  fetchEndpoint('/user/XFUser/GetSMSFromResetPassword', 'POST', false, {
      number : params.number
  })

//校验外部设置交易密码地址有效性
export const checkSetPwdUrlRequest = params =>
  fetchEndpoint('/user/XFUser/SetPassWordPage', 'POST', false, {
      token : params.token
  })

//校验外部校验交易密码地址有效性
export const checkTradersPwdUrlRequest = params =>
  fetchEndpoint('/user/XFUser/CheckPassWordPage', 'POST', false, {
      token : params.token
  })

  //外部校验交易密码
  export const checkTradersPwdRequest = params =>
    fetchEndpoint('/user/XFUser/CheckPassWord', 'POST', false, {
        token : params.token,
        password: params.password
    })



